<template>
    <div class='jf_explain'>
        <div class='jf_explain_head'>
            <div class="back"  @click.stop="back()">
                <img src="../../assets/image/联合42.png" alt="">
            </div>
            <div class="head">积分说明</div>
        </div>
        <div class='jf_explain_contain'>
            <div class='container_list' v-for="item in explainList" :key="item.code">
                <div class='list_top'>
                    <div class='shu'></div>
                    <div class='list_title'>{{item.title}}</div>
                </div>
                <div class='list_content'>
                    {{item.content}}积{{item.completeScore}}分，每日上限{{item.topScore}}分
                </div>
            </div>
        </div>
        <!-- 加载页面 -->
        <Loading :dialogVisible="verDialogVisible"></Loading>
    </div>
</template>
<script>
import Loading from './../components/loading'
import API from '@/api/api'
export default {
  name: 'index',
  components: {
    Loading
  },
  data () {
    return {
      explainList: [],
      verDialogVisible: true // 加载页
    }
  },
  created () {
    this.getindCreditsAndGs()
  },
  methods: {
    // 返回上一路由
    back () {
      this.$router.back()
    },
    // 获取积分规则数据
    getindCreditsAndGs () {
      API.post('findCreditsAndGs', {
      }, 'json', (data, err) => {
        if (data.success) {
          this.explainList = data.data.creditsAndGs // 积分规则列表
          this.verDialogVisible = false
        } else {
          alert(data.status)
        }
      })
    }
  }
}
</script>
<style scoped>
    .jf_explain{
        width:100%;
        height:100%;
    }
    .jf_explain_head{
        width:100%;
        background:#fff;
        font-size:0.36rem;
        font-family: PingFang-SC-Medium;
        position: fixed;
        top:0;
        height: 0.94rem;
        display: flex;
        justify-content: center;
        z-index: 99;
        border-bottom: 0.012rem solid #F5F6F8;
    }
    .jf_explain_head .back{
        width:0.89rem;
        height:0.94rem;
        position: fixed;
        top:0;
        left:0;
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 100;
    }
    .jf_explain_head .back img{
        width:0.19rem;
        height:0.34rem;
    }
    .jf_explain_head .head{
        line-height: 0.94rem;
        font-size:0.38rem;
        color:#4E4E4E;
        width:100%;
        padding-top:0.02rem;
        white-space:nowrap;
        text-align: center
    }
    .jf_explain .jf_explain_contain{
        margin-top:0.94rem;
        background: #fff;
        padding:0 0.35rem;
        width:100%;
        height:calc(100vh - 0.94rem);
        overflow: scroll;
        -webkit-overflow-scrolling: touch;
    }
    .container_list{
        padding:0.3rem 0 0.2rem 0;
        width:100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }
    .container_list .list_top{
        display: flex;
        align-items: center;
        margin-bottom: 0.16rem;
    }
    .container_list .list_top .shu{
        width:0.06rem;
        height:0.29rem;
        background: #5794F6;
        border-radius: 0.04rem;
    }
    .container_list .list_top .list_title{
        font-size: 0.32rem;
        color:#4E4E4E;
        margin-left:0.1rem;
        letter-spacing: 0.01rem;
    }
    .container_list .list_content{
        font-size: 0.3rem;
        color:#6E747E;
        line-height: 0.38rem;
        margin-left:0.16rem;
        letter-spacing: 0.01rem;
    }
</style>



